import React, { useState } from 'react'


export default function Textbox(props) {
    const [text, setText] = useState("");
    let words = 0;


    const changeText = (event) => {
        setText(event.target.value);
    }
    const upButtonClick = () => {
        let newText = text.toUpperCase();
        setText(newText)
    }
    const loButtonClick = () => {
        let newText = text.toLowerCase();
        setText(newText)
    }
    const resetButtonClick = () => {
        setText('')
    }
    const copyButtonClick = () => {
        navigator.clipboard.writeText(text);
    }


    if (text !== "") {
        words = text.split(" ").length
    }


    return (
        <div className="section1">
            <h2>Enter text to edit</h2>
            <textarea name="textarea" id="textarea" cols="50" rows="12" onChange={changeText} value={text}></textarea>
            <div className="other_details">
                <span>{text.length} Characters</span>
                <span>{words} Words</span>
                <span>{0.008 * words}m Read time</span>
            </div>
            <div className="edit_buttons">
                <div className="button" onClick={upButtonClick}>To UpperCase</div>
                <div className="button" onClick={loButtonClick}>To LowerCase</div>
                <div className="button" onClick={resetButtonClick}>Reset</div>
                <div className="button" onClick={copyButtonClick}>Copy Text</div>
            </div>
            
        </div>
    )
}
