import React from 'react'
import PropTypes from 'prop-types'


export default function Navbar(props) {
    return (
        <header>
            <div className="left" id="left">{props.title}</div>
            <div className="middle" id="middle">
                <ul>
                    <li id="homemenu">Home</li>
                    <li id="notemenu">Editor</li>
                    <li id="aboutmenu">About</li>
                </ul>
            </div>
            <div className="right">
                <div className="searchbar" id="searchbar">
                    <input type="search" name="search" id="search" placeholder="Search Notes" />
                </div>
                <div className="menubtn">
                    <svg xmlns="http://www.w3.org/2000/svg" width="28" height="28" id="menubtn" fill="currentColor"
                        className="bi bi-list" viewBox="0 0 16 16">
                        <path fillRule="evenodd"
                            d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z" />
                    </svg>
                </div>
            </div>
        </header>
    )
}

Navbar.propTypes = {
    title : PropTypes.string.isRequired
}

Navbar.defaultProps = {
    title : "Title"
}

